@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
* {
  margin: 0;
  bottom: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  color: white;
}
html,
body {
  height: 100%;
  width: 100%;
}

li {
  list-style: none;
}
a {
  text-decoration: none;
}

.content-container {
  height: 90%;
  width: 100%;
  padding: 1.4rem;
  position: absolute;
  top: 10%;
  right: 0;
  left: 0;
}
.content-container * {
  z-index: 1;
}

body {
  background-color: #191919;
}

@media screen and (max-width: 900px) {
  .content-container {
    width: 100%;
    left: 0;
    padding: 1.4rem;
  }
}

.app-router {
  z-index: 10;
}


